
import './App.css';
import Navbar from './components/Navbar';
import Form from './components/Form';
import { useState } from 'react/cjs/react.development';
import Alert from './components/Alert';
// import About from './components/About';

// import {
//   BrowserRouter as Router,
//   Switch,
//   Route,

// }from "react-router-dom";

function App() {
const [alert, setalert] = useState(null);

const showalert = (message, type)=> {
  setalert({
    msg: message,
    type: type
  })
}

setTimeout(() => {
 setalert(null);
},2000);

const [mode, setmode] = useState("light");

const [mystyle, setmystyle] = useState({
  color: "black",
  backgroundColor: "white"
})

const [bttext, setbttext] = useState("Enable Dark mode");

const handledark = () => {
  if(mystyle.color === "white"){
    setmystyle({
      color: "black",
      backgroundColor: "white"
    })
    document.body.style.backgroundColor = "white";
    setbttext("Enable Dark Mode");
    setmode("light");
    showalert(
    "Dark mode is disabled",
    "success"
    );
  }

  else{
    setmystyle({
      color: "white",
      backgroundColor: "#343a40"
    })
    document.body.style.backgroundColor = "grey";
   setbttext("Disable Dark Mode");
   setmode("dark");
   showalert(
    "Dark mode is enabled",
     "success"
  );
  }
};


  return (
    // <Router>
    <div>
    <Navbar  mystyle={mystyle} mode={mode}/>
    <Alert alert = {alert}/>
    <div className="container my-5">
    {/* <Switch>
      <Route exact path="/about">
      <About mystyle={mystyle}/>
      </Route>
      <Route exact path="/">
      <Form label="Enter your text Here" handledark={handledark} mystyle={mystyle} bttext={bttext} mode={mode} showalert={showalert}/>
      </Route>
    </Switch> */}
    
    <Form label="Enter your text Here" handledark={handledark} mystyle={mystyle} bttext={bttext} mode={mode} showalert={showalert}/>
     
    </div>
    </div>
    // </Router>
  );
}

export default App;
