import React, { useState } from "react";

export default function Form(props) {
  const [text, setText] = useState("");
 

  // const [bttext, setbttext] = useState("Enable dark mode");



  const handleupclick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    props.showalert("Converted to Uppercase", "success");
  };

  const handleonchange = (event) => {
    setText(event.target.value);
  };

  const handlelower = () => {
    let lower = text.toLowerCase();
    setText(lower);
    props.showalert("Converted to Lowercase", "success");
  };

  const handleclear = () => {
    setText("");
    props.showalert("All Clear", "success");
  };


  const handlecopy = () => {
    let newtext = document.getElementById("mybox");
    newtext.select();
    navigator.clipboard.writeText(newtext.value);
    props.showalert("Copied to Clopboard", "success");
  }
  

  const handlespace = () => {
    let newtext = text.split(/[ ]+/);
    setText(newtext.join(" "));
    props.showalert("Extra Spaces removed", "success");
  }



  return (
    <div>
      <div className="form-floating px-3 py-2" style={props.mystyle}>
        <h1 className="form-label" style={props.mystyle}>{props.label}</h1>
        <p style={props.mystyle}>
          {text.split(" ").length} Words and {text.length} Characters
        </p>
        <textarea
        style={props.mystyle}
          className="form-control py-2 h-25"
          onChange={handleonchange}
          rows="10"
          placeholder="Type here...."
          id="mybox"
          value={text}
        ></textarea>
        <button style={props.mystyle} className="btn btn-primary my-2" onClick={handleupclick}>
          Convert to Uppercase
        </button>
        <button style={props.mystyle} className="btn btn-primary my-2 mx-3" onClick={handlelower}>
          Convert to Lower
        </button>
        <button style={props.mystyle} className="btn btn-primary my-2 mx-3" onClick={handleclear}>
          Clear All
        </button>
        <button style={props.mystyle} className="btn btn-primary my-2 mx-3" onClick={handlecopy}>
          Copy Text
        </button>
        <button style={props.mystyle} className="btn btn-primary my-2 mx-3" onClick={handlespace}>
          Remove Extra Spaces
        </button>
        <div class="form-check form-switch px-5 py-2">
  <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick={props.handledark} />
  <label class="form-check-label" for="flexSwitchCheckDefault">{props.bttext}</label>
</div>
      </div>
      <div className="container my-4">
         <h2 style={{color: props.mode === "light"?"black":"white" }}>Preview</h2>
         <p style={{color: props.mode === "light"?"black":"white" }}>{text}</p>
      </div>
    </div>
  );
}
